import * as os from 'os'
import * as fs from 'fs'
import { spawn } from 'child_process'
import * as https from 'https'
import * as path from 'path'

const Q = require('q')
const debug = require('debug')('sqs-local')

const JARNAME = 'elasticmq-server-0.14.6.jar'

const Config = {
  installPath: path.join(os.tmpdir(), 'sqs-local'),
  downloadUrl: 'https://s3-eu-west-1.amazonaws.com/softwaremill-public/' + JARNAME,
}

const runningProcesses = {}

export const SQSLocal = {
  /**
   *
   * @param port
   * @param dbPath if omitted will use in memory
   * @param additionalArgs
   * @param verbose
   * @param detached
   * @returns {Promise.<ChildProcess>}
   */
  launch: function(port, dbPath, additionalArgs, verbose = false, detached) {
    if (runningProcesses[port]) {
      return Q.fcall(function() {
        return runningProcesses[port]
      })
    }

    if (!additionalArgs) {
      additionalArgs = []
    } else if (!Array.isArray(additionalArgs)) {
      additionalArgs = [additionalArgs]
    }

    if (!dbPath) {
      additionalArgs.push('-inMemory')
    } else {
      additionalArgs.push('-dbPath', dbPath)
    }

    return installSQSLocal().then(function() {
      var args = ['-Xrs', '-Djava.library.path=./SQSLocal_lib', '-jar', JARNAME, '-port', port]
      args = args.concat(additionalArgs)

      var child = spawn('java', args, {
        cwd: Config.installPath,
        env: process.env,
        stdio: ['pipe', 'pipe', process.stderr],
      })

      if (!child.pid) throw new Error('Unable to launch SQSLocal process')

      child
        .on('error', function(err) {
          if (verbose) debug('local DynamoDB start error', err)
          throw new Error('Local DynamoDB failed to start. ')
        })
        .on('close', function(code) {
          if (code !== null && code !== 0) {
            if (verbose) debug('Local DynamoDB failed to close with code', code)
          }
        })
      if (!detached) {
        process.on('exit', function() {
          child.kill()
        })
      }

      runningProcesses[port] = child

      if (verbose) {
        debug(
          'SQSLocal(',
          child.pid,
          ') started on port',
          port,
          'via java',
          args.join(' '),
          'from CWD',
          Config.installPath,
        )
      }

      return child
    })
  },
  stop: function(port) {
    if (runningProcesses[port]) {
      runningProcesses[port].kill('SIGKILL')
      delete runningProcesses[port]
    }
  },
  stopChild: function(child) {
    if (child.pid) {
      debug('stopped the Child')
      child.kill()
    }
  },
  relaunch: function(port, db) {
    this.stop(port)
    this.launch(port, db)
  },
  configureInstaller: function(conf) {
    if (conf.installPath) {
      Config.installPath = conf.installPath
    }
    if (conf.downloadUrl) {
      Config.downloadUrl = conf.downloadUrl
    }
  },
}

export default SQSLocal

export function installSQSLocal() {
  debug('Checking for SQS-Local in ', Config.installPath)
  //   var filebuf = []
  var deferred = Q.defer()

  try {
    if (fs.existsSync(path.join(Config.installPath, JARNAME))) {
      return Q.fcall(function() {
        return true
      })
    }
  } catch (e) {}

  debug('SQS Local not installed. Installing...')

  if (!fs.existsSync(Config.installPath)) fs.mkdirSync(Config.installPath)

  if (fs.existsSync(Config.downloadUrl)) {
    debug('Installing from local file:', Config.downloadUrl)
    const filebuf = fs.createReadStream(Config.downloadUrl)
    filebuf
      .on('end', function() {
        deferred.resolve()
      })
      .on('error', function(err) {
        deferred.reject(err)
      })
  } else {
    https
      .get(Config.downloadUrl, function(redirectResponse) {
        if (200 != redirectResponse.statusCode) {
          deferred.reject(
            new Error(
              'Error getting SQS local latest tar.gz location ' +
                redirectResponse.headers['location'] +
                ': ' +
                redirectResponse.statusCode,
            ),
          )
        }
        redirectResponse
          .on('end', function() {
            deferred.resolve()
          })
          .on('error', function(err) {
            deferred.reject(err)
          })
      })
      .on('error', function(e) {
        deferred.reject(e)
      })
  }
  return deferred.promise
}
